import React, { useReducer } from "react";
import ChitInput from "./components/ChitInput";
import Deposits from "./components/Deposits";
import "./styles.scss";
import { textsChit } from "./constants";
import ChitOutput from "./components/ChitOutput";
import { chitReducer } from "./hooks";
import { initialState } from "./constants";
import {
  calcChitAuctionResult,
  calcChitStatus,
  calcDepositInterestTotal
} from "./helpers";
import { toNumber } from "shared/helpers/number";
import { useMemoizedCSSClass } from "shared/hooks/styles";

export default function Chit() {
  const [{ chitInput, deposits }, dispatch] = useReducer(
    chitReducer,
    initialState
  );
  const chitStatus = calcChitStatus(chitInput);
  // Deposits are effective only from next month
  const depositsDuration = toNumber(chitStatus.noOfMonthsRemaining) - 1;
  const depositInterestTotal = calcDepositInterestTotal(
    deposits,
    depositsDuration
  );
  const chitAuctionResult = calcChitAuctionResult(
    chitInput,
    chitStatus,
    depositInterestTotal
  );
  const diffAmountWithNormalChitTotalInHand = toNumber(
    chitAuctionResult.diffAmountWithNormalChitTotalInHand
  );
  const diffAmountClassName = useMemoizedCSSClass(
    [
      {
        "text-red-500": diffAmountWithNormalChitTotalInHand < 0,
        "text-green-500": diffAmountWithNormalChitTotalInHand > 0
      }
    ],
    [diffAmountWithNormalChitTotalInHand]
  );

  return (
    <div className="chit">
      <div className="chit-input-container">
        <ChitInput
          values={chitInput}
          onInputChange={(event, actionType) => {
            const value = event.target.value ?? "";
            dispatch([actionType, value]);
          }}
        />
        <div>
          <Deposits
            deposits={deposits}
            onInputChange={(event, actionType, index) => {
              const value = event.target.value ?? "";
              dispatch([actionType, { index, value }]);
            }}
          />
          {depositInterestTotal > 0 && (
            <>
              {textsChit.depositInterestTotalAmount.replace(
                "{months}",
                depositsDuration.toString()
              )}
              : {depositInterestTotal}
            </>
          )}
        </div>
      </div>
      <div className="chit-output-container">
        <ChitOutput
          results={[
            {
              text: textsChit.chitTotal,
              value: chitStatus.chitTotal
            },
            {
              text: textsChit.normalChitTotalInHand,
              value: chitStatus.normalChitTotalInHand
            },
            {
              text: textsChit.noOfMonthsRemaining,
              value: chitStatus.noOfMonthsRemaining
            },
            {
              text: textsChit.lastAuctionAmount,
              value: chitStatus.lastAuctionAmount
            },
            {
              text: textsChit.agencyCommissionAmount,
              value: chitStatus.agencyCommissionAmount
            }
          ]}
          title={textsChit.chitStatus}
        />
        <ChitOutput
          results={[
            {
              text: textsChit.auctionedChitTotalInHand,
              value: chitAuctionResult.auctionedChitTotalInHand
            },
            {
              text: textsChit.totalSavingsByAuction,
              value: chitAuctionResult.totalSavingsByAuction
            },
            {
              text: textsChit.diffAmountWithNormalChitTotalInHand,
              value: chitAuctionResult.diffAmountWithNormalChitTotalInHand,
              valueClassName: diffAmountClassName
            }
          ]}
          title={textsChit.chitAuctionResult}
        />
      </div>
    </div>
  );
}
