export type T_ChitInput = Record<
  | "monthlyPaymentAmount"
  | "duration"
  | "noOfPaymentsSoFar"
  | "lastPaymentAmount"
  | "agencyCommissionPercent"
  | "yourAuctionAmount",
  string
>;

export type T_DepositField = "depositAmount" | "depositInterestPercent";

export type T_Deposit = Record<T_DepositField, string>;

export type T_Deposits = T_Deposit[];

export type T_ChitState = {
  chitInput: T_ChitInput;
  deposits: T_Deposits;
};

export enum E_ChitActionType {
  clearEverything,
  inputMonthlyPaymentAmount,
  inputDuration,
  inputNoOfPaymentsSoFar,
  inputLastPaymentAmount,
  inputAgencyCommissionPercent,
  inputYourAuctionAmount,
  inputDepositAmount,
  inputDepositInterestPercent,
  addDeposit,
  removeDeposit
}

export type T_ChitInputActionType =
  | E_ChitActionType.inputMonthlyPaymentAmount
  | E_ChitActionType.inputDuration
  | E_ChitActionType.inputNoOfPaymentsSoFar
  | E_ChitActionType.inputLastPaymentAmount
  | E_ChitActionType.inputAgencyCommissionPercent
  | E_ChitActionType.inputYourAuctionAmount;

export type T_ChitInputDepositActionType =
  | E_ChitActionType.inputDepositAmount
  | E_ChitActionType.inputDepositInterestPercent;

export type T_ChitDepositIndex = number;

export type T_ChitAction =
  | [E_ChitActionType.clearEverything]
  | [T_ChitInputActionType, string]
  | [
      T_ChitInputDepositActionType,
      {
        index: T_ChitDepositIndex;
        value: string;
      }
    ]
  | [E_ChitActionType.addDeposit, T_Deposits]
  | [E_ChitActionType.removeDeposit, T_ChitDepositIndex];

export type T_ChitStatus = {
  chitTotal: number;
  normalChitTotalInHand: number;
  noOfMonthsRemaining: number;
  lastAuctionAmount: number;
  agencyCommissionAmount: number;
};
