import React from "react";
import { toHTMLElementID } from "shared/helpers/string";
import { textsChit } from "../constants";
import "../styles.scss";
import {
  E_ChitActionType,
  T_ChitDepositIndex,
  T_ChitInputDepositActionType,
  T_Deposits
} from "../types";

export default function Deposits({
  deposits,
  onInputChange
}: {
  deposits: T_Deposits;
  onInputChange: (
    event: React.ChangeEvent<HTMLInputElement>,
    actionType: T_ChitInputDepositActionType,
    index: T_ChitDepositIndex
  ) => void;
}) {
  return (
    <section className="deposits">
      <h2>{textsChit.deposits}</h2>
      {deposits.map((_d, depositIndex) => (
        <form key={depositIndex} className="deposit-item">
          {[
            {
              text: textsChit.depositAmount,
              id: toHTMLElementID(textsChit.depositAmount),
              actionType: E_ChitActionType.inputDepositAmount
            },
            {
              text: textsChit.depositInterestPercent,
              id: toHTMLElementID(textsChit.depositInterestPercent),
              actionType: E_ChitActionType.inputDepositInterestPercent
            }
          ].map(({ text, id, actionType }) => (
            <fieldset key={id}>
              <label htmlFor={id}>{text}</label>
              <input
                type="text"
                id={id}
                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                  onInputChange(
                    event,
                    actionType as T_ChitInputDepositActionType,
                    depositIndex
                  );
                }}
              />
            </fieldset>
          ))}
          <button type="button" className="deposit-remove-button">X</button>
        </form>
      ))}
    </section>
  );
}
