import React from "react";
import { toHTMLElementID } from "shared/helpers/string";
import { textsChit } from "../constants";
import "../styles.scss";
import { E_ChitActionType, T_ChitInput, T_ChitInputActionType } from "../types";

export default function ChitInput({
  onInputChange,
  values
}: {
  onInputChange: (
    event: React.ChangeEvent<HTMLInputElement>,
    actionType: T_ChitInputActionType
  ) => void;
  values: T_ChitInput;
}) {
  return (
    <form className="chit-input-form">
      {[
        {
          text: textsChit.monthlyPaymentAmount,
          id: toHTMLElementID(textsChit.monthlyPaymentAmount),
          actionType: E_ChitActionType.inputMonthlyPaymentAmount,
          value: values.monthlyPaymentAmount
        },
        {
          text: textsChit.duration,
          id: toHTMLElementID(textsChit.duration),
          actionType: E_ChitActionType.inputDuration,
          value: values.duration
        },
        {
          text: textsChit.noOfPaymentsSoFar,
          id: toHTMLElementID(textsChit.noOfPaymentsSoFar),
          actionType: E_ChitActionType.inputNoOfPaymentsSoFar,
          value: values.noOfPaymentsSoFar
        },
        {
          text: textsChit.lastPaymentAmount,
          id: toHTMLElementID(textsChit.lastPaymentAmount),
          actionType: E_ChitActionType.inputLastPaymentAmount,
          value: values.lastPaymentAmount
        },
        {
          text: textsChit.agencyCommissionPercent,
          id: toHTMLElementID(textsChit.agencyCommissionPercent),
          actionType: E_ChitActionType.inputAgencyCommissionPercent,
          value: values.agencyCommissionPercent
        },
        {
          text: textsChit.yourAuctionAmount,
          id: toHTMLElementID(textsChit.yourAuctionAmount),
          actionType: E_ChitActionType.inputYourAuctionAmount,
          value: values.yourAuctionAmount
        }
      ].map(({ value, text, id, actionType }) => (
        <fieldset key={id}>
          <label htmlFor={id}>{text}</label>
          <input
            type="text"
            id={id}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              onInputChange(event, actionType as T_ChitInputActionType);
            }}
            value={value}
          />
        </fieldset>
      ))}
    </form>
  );
}
