// @ts-nocheck
import { initialState, textsChit } from "./constants";
import {
  E_ChitActionType,
  T_ChitAction,
  T_ChitDepositIndex,
  T_ChitState,
  T_DepositField,
  T_Deposits
} from "./types";

export function chitReducer(
  state: T_ChitState,
  [type, payload]: T_ChitAction
): T_ChitState {
  switch (type) {
    case E_ChitActionType.clearEverything: {
      return initialState;
    }
    case E_ChitActionType.inputMonthlyPaymentAmount: {
      return {
        ...state,
        chitInput: {
          ...state.chitInput,
          monthlyPaymentAmount: payload
        }
      };
    }
    case E_ChitActionType.inputDuration: {
      return {
        ...state,
        chitInput: {
          ...state.chitInput,
          duration: payload
        }
      };
    }
    case E_ChitActionType.inputNoOfPaymentsSoFar: {
      return {
        ...state,
        chitInput: {
          ...state.chitInput,
          noOfPaymentsSoFar: payload
        }
      };
    }
    case E_ChitActionType.inputLastPaymentAmount: {
      return {
        ...state,
        chitInput: {
          ...state.chitInput,
          lastPaymentAmount: payload
        }
      };
    }
    case E_ChitActionType.inputAgencyCommissionPercent: {
      return {
        ...state,
        chitInput: {
          ...state.chitInput,
          agencyCommissionPercent: payload
        }
      };
    }
    case E_ChitActionType.inputYourAuctionAmount: {
      return {
        ...state,
        chitInput: {
          ...state.chitInput,
          yourAuctionAmount: payload
        }
      };
    }
    case E_ChitActionType.inputDepositAmount: {
      const newDeposits = getUpdatedDeposits(state.deposits, {
        ...payload,
        field: "depositAmount"
      });
      return {
        ...state,
        deposits: newDeposits
      };
    }
    case E_ChitActionType.inputDepositInterestPercent: {
      const newDeposits = getUpdatedDeposits(state.deposits, {
        ...payload,
        field: "depositInterestPercent"
      });
      return {
        ...state,
        deposits: newDeposits
      };
    }
    case E_ChitActionType.addDeposit: {
      const newDeposits = state.deposits.concat(initialState.deposits[0]);
      return {
        ...state,
        deposits: newDeposits
      };
    }
    case E_ChitActionType.removeDeposit: {
      const newDeposits = state.deposits.filter(
        (_d, index) => index !== payload
      );
      return {
        ...state,
        deposits: newDeposits
      };
    }
    default: {
      throw new Error(textsChit.chitReducerError);
    }
  }
}

function getUpdatedDeposits(
  deposits: T_Deposits,
  updatedDeposit: {
    index: T_ChitDepositIndex;
    field: T_DepositField;
    value: string;
  }
) {
  return deposits.map((deposit, index) => {
    if (updatedDeposit.index === index) {
      return {
        ...deposit,
        [updatedDeposit.field]: updatedDeposit.value
      };
    }
    return deposit;
  });
}
